import axios from 'axios';

axios.defaults.baseURL = 'https://hn.algolia.com/api/v1';

export const fetchArticlesWithQuery = async (searchQuery, page = 0) => {
  const response = await axios.get(`/search?query=${searchQuery}&page=${page}`);
  return response.data;
};

export default {
  fetchArticlesWithQuery,
};